@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #eee;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
   position: -webkit-sticky;
   position: sticky;
   top:0;
   background-color: #fff;
   width:100%;
   height:100px;
   font-family: 'Baumans',cursive;
   display: -webkit-flex;
   display: flex;
   -webkit-align-items: center;
           align-items: center;
   box-shadow: 0px 0px 10px black;
   z-index: 100;
}

.menuIcon{
   margin-left:30px;
   cursor: pointer;
   z-index: 1;
   padding:10px;   
}
/* #MuiIcon-fontSizeLarge{
    font-size: 35px;
} */
.menuIcon:hover{
    background-color: rgb(241, 241, 241);
    border-radius: 50%;
}
.header >.title{
    font-size: 40px;
    font-weight: 600;
    width:100%;
    /* background-color: yellow; */
    text-align: center;
    margin-left: -80px;
    /* cursor: pointer; */
}
.header >.title > a{
    text-decoration: none;
    color:black
}
.side-nav{
    position: fixed;
    background-color: #466AFF;
    height:90vh;
    overflow-y:auto ;
    width:300px;
    border-radius: 40px;
    padding:10px 20px;
    border-bottom: 2px solid white;
    transition:0.5s;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
.menuIcon > #MuiIcon-fontSizeLarge{
    font-size: 3vh;
}
.nav-sub > #MuiIcon-fontSizeLarge{
    font-size: 3vh;
}
.add-sub, .nav-sub{
    margin-top: 25px;
    font-size: 40px;
    font-family: 'Bebas Neue';
    border-bottom: 1px solid white;
    cursor: pointer;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.add-sub > a,.nav-sub > a{
    text-decoration: none;
    color:white;
}
.nav-sub{
    margin-top: 10px;
}
.subjects{
    text-align: center;
    padding-top: 15px;
    padding-bottom: 10px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-size: 40px;
    font-family: 'Bebas Neue';
    border-bottom: 1px solid white;
}
.subjectsList > li{
    list-style: none;
}
.toggle{
    /* left:-300px; */
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity: 1;
}
@media (max-width:1450px){
    .header{
        height:65px
    }
    .header > .title {
        font-size: 30px;
    }
    .MuiIcon-fontSizeLarge{
        font-size: 25px;
    }
    .side-nav{
        width:200px;
        border-radius: 30px;
        padding:7px 10px;
    }
    .add-sub, .nav-sub, .subjects{
        font-size: 30px;
    }
}
@media (max-width:1100px){
    .header > .title {
        font-size: 20px;
    }
    .side-nav {
        width:150px;
        border-radius: 20px;
        padding:7px 10px;
    }
    .add-sub, .nav-sub, .subjects{
        font-size: 20px;
    }
}
@media(max-width:850px){
    .header{
        height:40px;
    }
    .header > .title {
        font-size:15px;
    }
    .side-nav {
        width:100px;
        border-radius: 15px;
        padding:0px 5px;
    }
    .add-sub, .nav-sub, .subjects{
        font-size: 13px;
    }
    .subjects{
        padding:5px 0px;
    }
    .add-sub{
        margin-top: 10px;
    }
}
.main{
    font-family: 'Bebas Neue', cursive;
    height:calc(100vh - 180px);
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.main > h1{
    margin-bottom:15px;
    font-size: 70px;
    font-weight: 200;
}
.main > div > form{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.main > div > form >input {
    height:64px;
    font-size: 40px;
    padding:20px;
    border:none;
    outline: none;
    box-shadow: 2px 2px 5px black;
}
#main-button{
    box-shadow: 2px 2px 5px black;  
    height:64px;
    font-size: 40px;
    padding:20px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    background: #466AFF;
    border:none;
    outline: none;
    cursor: pointer;
}
#main-button:hover{
    background-color: rgb(0, 0, 255)   ;
}
@media (max-width:1450px){
    .main > h1{
        font-size: 45px;
    }
    .main > div > form >input {
        font-size: 30px;
        height:50px;    
    }
    #main-button{
        height:50px
    }
}
@media (max-width:1100px){
    .main > h1{
        font-size: 35px;
    }
    .main > div > form >input {
        font-size: 20px;
        height:40px;    
    }
    #main-button{
        height:40px;
        font-size: 30px;
    }
}
@media (max-width:850px){
    .main{
        height:calc(100vh - 50px); ;
    }
    .main > h1{
        font-size: 20px;
    }
    .main > div > form >input {
        font-size: 12px;
        height:25px;    
        padding:10px
    }
    #main-button{
        height:25px;
        font-size: 18px;
        padding:10px
    }
}
.align-center{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    min-height: 75px;
    border-bottom: 2px solid black;
    text-transform: uppercase;
}
.align-center > h2{
    margin-top: 20px;
}
.subjects-input{
    font-size: 30px;
    padding: 5px;
    border: none;
}
.subjects-flex{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 20px;
    margin-top: 20px;
}
.subjects-select{
    margin-left:15px ;
    font-size: 30px;
    padding: 5px;
}
.subject-container{
    font-size: 25px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    margin:20px;
}

.total, .class, .home{
    padding-top: 15px;
    min-width: 600px;
    min-height:500px;
    text-align: center;
    text-transform: uppercase;
}
.total > h3, .class >h3, .home > h3{
   font-family: 'Baumans',cursive;
}
.total, .class{
    border-right: 2px solid black;
}
.class{
    padding-right: 20px;
}
#sub-button{
    height:45px;
    font-size: 40px;
    padding:10px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    background: #466AFF;
    border:none;
    outline: none;
    cursor: pointer;
}
#sub-button:hover{
    background-color: rgb(0, 0, 255);
}
.chapters{
    /* width:600px; */
    background-color: white;
    margin-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
    margin-right: 20px;
    text-align: left; 
    border:1px solid black;
    height:auto;
}
.chapter-align{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content:space-between;
            justify-content:space-between;

}
.chapter-delete{
    cursor: pointer;
}
.delAll{
    padding:10px;
    font-size: 22px;
    background: red;
    outline: none;
    border: none;
    border-radius: 999px;
    color:white;
    margin-top: 10px;
    cursor: pointer;
}
.delAll:hover{
    background-color: #ce2121;

}
.chapter-align > #MuiIcon-fontSizeLarge{
    font-size: 25px;
}
.class-record{
    margin-top: 77px;
    border:1px solid black
}
.record-icons{
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    width:100px;
    cursor:pointer;
}
.done{
    background-color:yellowgreen;
    width:50%
}
.clear{
    background-color: #ff0000;
    width:50%
}
.done > #MuiIcon-fontSizeLarge{
    font-size: 25px;
}
.clear > #MuiIcon-fontSizeLarge{
    font-size: 25px;
}
.done:hover{
    background-color:   #AED75B;
}
.clear:hover{
    background-color: #ce2121;
}
.record-box{
    background-color: #fff;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding-left: 20px;
}

