.mobBtn{
    display: none;
    text-align: center;
    margin: 10px;
    font-size: 10px;
}
.mobBtn > button {
    outline: none;
    border:none;
    cursor: pointer;
    color:white;
    font-family: Bebas Neue;
    letter-spacing: 2px;
}
.totalBtn{
    margin-left: 10px;
    background: #466AFF;
    padding:5px    
}

